@font-face {
  font-family: "Rubik-Black";
  src: url("../fonts/Rubik-Black.woff2");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik-Bold";
  src: url("../fonts/Rubik-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik-SemiBold";
  src: url("../fonts/Rubik-SemiBold.woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik-Regular";
  src: url("../fonts/Rubik-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter-Regular";
  src: url("../fonts/Inter-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Rubik-Regular", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
}
body.lock {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1670px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.calc {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 45px 30px 30px 25px;
  width: 100%;
  max-width: 480px;
  border-radius: 15px;
  background: #71543F;
  backdrop-filter: blur(3.5px);
}
.calc__title {
  margin: 0 auto;
}
.calc__inner-range {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  width: 100%;
}
.calc__subinner-range {
  padding: 18px 10px;
  width: 100%;
  max-width: 320px;
  border-radius: 15px;
  background-color: #ffffff;
  line-height: 0;
}
.calc__range {
  appearance: none;
  width: 100%;
  cursor: pointer;
  outline: none;
  height: 4px;
  border-radius: 4px;
  background: linear-gradient(to right, #5A483F 0%, rgba(0, 0, 0, 0.15) 0%);
  position: relative;
}
.calc__range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  height: 15px;
  width: 15px;
  background-color: #ffffff;
  border: 2px solid #71543F;
  border-radius: 50%;
  transition: 0.2s ease-in-out;
  z-index: 1;
  position: relative;
}
.calc__range::-moz-range-thumb {
  -webkit-appearance: none;
  appearance: none;
  height: 15px;
  width: 15px;
  background-color: #ffffff;
  border: 2px solid #71543F;
  transition: 0.2s ease-in-out;
  z-index: 1;
  position: relative;
}
.calc__inner-input {
  padding: 9px 28px;
  max-width: 100px;
  border-radius: 15px;
  background: #ffffff;
  position: relative;
}
.calc__input {
  padding: 0;
  max-width: 40px;
  color: #000000;
  font-family: "Inter-Regular", sans-serif;
  font-weight: 400;
  font-size: 20px;
  font-weight: 400;
  border: none;
  text-align: center;
}
.calc__button {
  position: absolute;
  width: 12px;
  height: 12px;
  top: 50%;
  right: 7px;
  transform: translateY(-50%);
  transition: transform 0.4s;
}
.calc__button::after {
  position: absolute;
  content: "";
  width: 100%;
  border-top: 2px solid #5A483F;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.calc__button::before {
  display: flex;
  position: absolute;
  content: "";
  height: 100%;
  border-left: 2px solid #5A483F;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.calc__button:hover {
  transform: translateY(-50%) scale(1.2);
}
.calc__button--minus {
  left: 7px;
}
.calc__button--minus::before {
  display: none;
}
.calc__inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 15px;
  padding: 20px 0;
  width: 100%;
  position: relative;
}
.calc__inner::after {
  position: absolute;
  content: "";
  width: 100%;
  border-top: 1px solid #ffffff;
  top: 0;
  left: 0;
}
.calc__inner::before {
  position: absolute;
  content: "";
  width: 100%;
  border-top: 1px solid #ffffff;
  bottom: 0;
  left: 0;
}
.calc__types {
  display: grid;
  grid-template-columns: repeat(2, auto);
  row-gap: 5px;
  column-gap: 100px;
  width: 100%;
}
.calc__subinner {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 27px;
}
.calc__radio {
  display: none;
  margin-right: 10pxr;
}
.calc__radio:checked + .calc__descr::after {
  display: flex;
}
.calc__descr {
  font-size: 21px;
  line-height: normal;
  cursor: pointer;
  position: relative;
}
.calc__descr::after {
  display: none;
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ffffff;
  top: 50%;
  left: -22px;
  transform: translateY(-50%);
}
.calc__descr::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  top: 50%;
  left: -27px;
  transform: translateY(-50%);
  transition: box-shadow 0.2s;
}
.calc__descr:active::after {
  display: flex;
  background-color: rgba(255, 255, 255, 0.5);
}
.calc__descr:hover::before {
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}
.calc__sum {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
  font-family: "Rubik-SemiBold", sans-serif;
  font-size: 40px;
  font-weight: 600;
  line-height: normal;
}
.calc__phone {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  margin-bottom: 25px;
  width: 100%;
}
.calc__number {
  padding: 15px 180px 15px 20px;
  width: 100%;
  max-width: 420px;
  color: #ffffff;
  font-size: 24px;
  line-height: normal;
  background-color: transparent;
  border-radius: 24px;
  border: 1px solid rgba(246, 251, 253, 0.28);
}
.calc__number::placeholder {
  color: rgba(255, 255, 255, 0.35);
}
.calc__btn {
  padding: 13.5px 30px;
  font-size: 22px;
  line-height: 150%;
  border-radius: 99px;
  background-color: #694F41;
  position: absolute;
  top: 0;
  right: 0;
  transition: all 0.4s;
}
.calc__btn:hover {
  color: #694F41;
  background-color: #ffffff;
}
.calc__policy {
  margin: 0;
  font-size: 12px;
  line-height: normal;
}
.calc__link {
  text-decoration-line: underline;
  position: relative;
  transition: color 0.2s;
}
.calc__link::after {
  position: absolute;
  content: "";
  width: 100%;
  border-top: 1px solid #ffffff;
  bottom: 0;
  left: 0;
  transition: border-top 0.2s;
}
.calc__link:hover {
  color: rgba(255, 255, 255, 0.7);
}
.calc__link:hover::after {
  border-top: 1px solid rgba(255, 255, 255, 0.7);
}
.calc .title {
  margin: 0;
  margin-bottom: 25px;
  font-size: 32px;
  font-style: normal;
  font-family: "Rubik-Black", sans-serif;
  font-weight: 900;
  line-height: 140%;
}

.hero {
  padding-top: 60px;
  width: 100%;
  max-width: 100vw;
  min-height: 100vh;
  background-image: url(../img/hero/background.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.hero__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero__inner {
  padding: 65px 100px;
  width: 100%;
  max-width: 845px;
  border-radius: 25px;
  background: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(3.5px);
}
.hero__title {
  margin: 0;
  margin-bottom: 50px;
  font-size: 64px;
  font-style: normal;
  font-family: "Rubik-Bold", sans-serif;
  font-weight: 700;
  line-height: 140%;
}
.hero__text {
  margin: 0;
  margin-bottom: 75px;
  font-size: 28px;
  font-weight: 400;
  line-height: 140%;
}
.hero__discount {
  font-family: "Rubik-Bold", sans-serif;
  font-weight: 700;
}
.hero__links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 615px;
}
.hero__link {
  padding: 15px 35px;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  border-radius: 99px;
  background: #694F41;
  transition: all 0.4s;
}
.hero__link:hover {
  color: #694F41;
  background-color: #ffffff;
}
.hero__link--transparent {
  border: 1px solid #ffffff;
  background-color: transparent;
}

.survey {
  padding-top: 60px;
  width: 100%;
  height: 100vh;
  background-image: url(../img/hero/background.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.quiz {
  padding: 40px 60px;
  width: 100%;
  max-width: 880px;
  min-height: 545px;
}